<template>
    <!-- 左右结果 -->
    <div
        :class="`parallel-box ${position}`"
    >
        <div class="parallel-text">
            <div class="sub-title">
                <p class="name">{{ name }}</p>
                <p class="intro">{{ intro }}</p>
            </div>

            <p class="cont">{{ cont }}</p>
        </div>
        <div class="img">
            <img
                :src="imgUrl"
                :style="{ width: (imgWidth/remUnit) + 'rem'}"
            />
        </div>
    </div>
</template>

<script>
import Common from '@/components/Common'
export default {
    name: "Parallel",

    props: {
        // scroll: {
        //     type: BarProp,
        //     default: false
        // },
        name: {
            type: String,
            default: "",
        },
        intro: {
            type: String,
            default: "",
        },
        cont: {
            type: String,
            default: "",
        },
        position: {
            type: String,
            default: "leftBox",
        },
        imgUrl: {
            type: String,
            default: "",
        },
        imgWidth: {
            type: Number,
            default: 0
        },
        imgHeight: {
            type: String,
            default: "385",
        },
        // boxHeight: {
        //     type: String,
        //     default: "",
        // },
    },

    data() {
        return {
            remUnit:Common.remUnit
        };
    },

    methods: {},
};
</script>

<style lang="scss" scoped>
.parallel-box {
    padding: 0 50px;
    // background-color: #fff;
    margin-bottom: 60px;
    text-align: left;
    .parallel-text {
        .sub-title {
            margin-bottom: 30px;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                background: #3a80e5;
                border-radius: 4.5px;
                display: block;
                width: 4px;
                height: 20px;
                left: 0;
                top: 20px;
                // transform: translateY(-50%);
            }
        }
        .name {
            font-size: 34px;
            line-height: 40px;
            color: #14202b;
            padding-left:20px;
        }
        .intro {
            font-size: 26px;
            line-height: 50px;
            padding-left: 30px;
        }
        .cont {
            font-size: 24px;
            line-height: 40px;
        }
    }

    .img{
        text-align: center;
        margin-top: 30px;
        padding-bottom: 10px;
    }
}
// .leftBox {
//     flex-direction: row;
//     .text {
//         flex: 1;
//     }
// }
// .rightBox {
//     flex-direction: row-reverse;
//     .img {
//         flex: 1;
//     }
// }
// .centerBox {
//     flex-direction: column;
//     .text {
//         margin-bottom: 50px;
//         .cont {
//             width: 100%;
//         }
//     }
// }
</style>



